@import '../../scss';

@mixin skeleton() {

  &.skeleton {
    pointer-events: none;
    user-select: none;
    display: block;
    box-sizing: content-box;
    background: $pds-theme-light-contrast-low;

    phn-p-heading {
      visibility: hidden;

      &:empty::after,
      & *:empty::after {
        content: "\00a0";
      }
    }
  }
}

:host {
  display: flex;
  flex-direction: column;
  align-items: center;

  phn-p-heading {
    @include pds-heading-large;

    @include pds-media-query-min('xs') {
      @include pds-heading-x-large;
    }
  }

  &.dealer-heading {
    phn-p-heading {

      @include pds-media-query-min('s') {
        font-size: p-px-to-rem(30px);
        line-height: p-px-to-rem(40px);
      }

      @include pds-media-query-min('m') {
        font-size: p-px-to-rem(28px);
        line-height: p-px-to-rem(40px);
      }
    }
  }

  div.text-limit {

    @include pds-media-query-min('base') {
      width: 180px;
    }

    @include pds-media-query-min('xs') {
      width: 100%;
    }

  }
}
