import { Component, h, Host, JSX, Prop } from '@stencil/core';
import { isString } from '../../utility/helper';

@Component({
  tag: 'phn-heading',
  styleUrl: 'heading.scss',
  scoped: true
})
export class Heading {
  @Prop() text!: string;
  @Prop() subtext?: string;

  render(): JSX.Element {
    return (
      <Host>
        <phn-p-heading size="inherit">{this.text}</phn-p-heading>
        {isString(this.subtext) ? (
          <div class="text-limit">
            <phn-p-text color="contrast-high" size="xx-small" tag="div" ellipsis="true">
              {this.subtext}
            </phn-p-text>
          </div>
        ) : null}
      </Host>
    );
  }
}
